.container{
    display: block;
    overflow: hidden;
    position: relative;
}
.yellowGrilleImg{
    position: absolute;
    top: 350px;
    left: -50px;
    height: 250px;
}
.blueGrilleImg{
    position: absolute;
    top: 800px;
    right: -100px;
    height: 250px;
}
.about{
    display: flex;
    background-color:white;
    align-items: center;
    justify-content: space-around;
    padding: 50px 50px;
}
.about .img_about{
    width: 370px;
    height: 380px;
    border-radius: 50px;
    margin: 0px 0;
    animation:mainImgAnimation1 6s infinite ease-in-out 10s forwards ;
}
@keyframes mainImgAnimation1{
    0%{
        scale: 1;
    }
    50%{
        scale: 1.2;
    }
    100%{
        scale: 1;
    }
}
.description{
    width: 50%;
    
}
.description h1{
    color: black;
    position: relative;
}
.description h1 .yellowLine{
    position: absolute;
    left: 0;
    bottom: 0;
}
.description p{
    color: black;
    opacity: 0.8;
}
.purpose{
    display: flex;
    justify-content: space-around;
    padding: 50px 50px;
    column-gap: 25px;
}
.img_purpose{
    width: 370px;
    height: 380px;
    margin-top: -50px;
    animation:mainImgAnimation2 7s infinite ease-in-out 5s forwards ;
}
@keyframes mainImgAnimation2{
    0%{
        scale: 1;
    }
    50%{
        scale: 1.2;
    }
    100%{
        scale: 1;
    }
}

.members{
    display: flex;
    background: rgb(255,255,255);
    background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(208, 221, 255,0.9051995798319328) 50%, rgba(255,255,255,1) 100%);
    flex-direction: column;
    padding: 0 50px 50px 50px;
    column-gap: 25px;
    overflow: hidden;
}
.members h1{
    margin-top: 25px;
    text-align: center;
    color: black;
    position: relative;
}
.yellowSolidLine{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 200px;
}
.members p{
    text-align: center;
    color: black;
    opacity: 0.8;
}

.doctors_cards{
    margin: 25px 0;
    display: flex;
    column-gap: 25px;
    row-gap: 25px;
}

.title{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 0 10px;
}
.title h1{
    font-size: 2.5rem;
    color: black;
}
.title p{
    font-size: 3rem;
    color: rgba(0, 0, 0, 0.575);
}
.accordeon_block{
    background-color:RGB(208, 221, 255);
    padding: 0 15px;
    border-radius: 25px;
    margin-bottom: 15px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.check{
    width: 20px;
    height: 20px;
    position: relative;
    
}
.first_line{
    height: 3px;
    width: 12px;
    background-color:black;
    rotate: 35deg;
    position: absolute;
    top: 8px;
    transition:all .5s ease;
}
.first_line_active{
    height: 3px;
    width: 12px;
    background-color:black;
    rotate: 0;
    position: absolute;
    top: 8px;
    transition:all .5s ease;
}
.second_line{
    height: 3px;
    width: 12px;
    background-color:black;
    rotate: -35deg;
    position: absolute;
    top: 8px;
    right: 0;
    transition:all .5s ease;
}
.second_line_active{
    height: 3px;
    width: 12px;
    background-color:black;
    rotate: -0;
    position: absolute;
    top: 8px;
    right: 0;
    transition:all .5s ease;
}


.content{
    height: 0;
    transition:all .5s ease;
    overflow: hidden;
    opacity: 1;
}
.content_active{
    height: 240px;
    transition: all .5s ease;
    opacity: 1;
    overflow: hidden;
}
.doctor_card h2{
    color: black;
    font-size: 2rem;
}
.doctor_card p{
    color: black;
    padding-bottom: 10px;
    text-align: center;
    opacity: 0.8;
}
.desc{
    position: absolute;
    bottom:85px;
    background-color: white;
    opacity: 0;
    transition: .3s all;
}



@media(max-width:915px){
    .container{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        column-gap: 50px;
        padding: 0 0px;
        overflow: hidden;
    }
    .yellowGrilleImg{
        display: none;
    }
    .blueGrilleImg{
        display: none;
    }
    .about{
        display: flex;
        background-color:white;
        flex-direction: column-reverse;
        padding: 0 10px 25px 10px;
        column-gap: 25px;
    }
    .description{
        width: 100%;
        margin-top: 25px;
    }
    .img_about{
        width: 90%;
        height: 40%;
        border-radius: 50px;
        margin: 25px auto;
    }
    .img_purpose{
        width: 90%;
        height: 40%;
        border-radius: 50px;
        margin: 25px auto;
    }
    .purpose{
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 0 10px 25px 10px;
        column-gap: 25px;
    }
    .title h1{
        font-size: 30px;
        color: black;
    }
    .content{
        height: 0;
        transition:all .5s ease;
        overflow: hidden;
        opacity: 1;
    }
    .content_active{
        height: 430px;
        transition: all .5s ease;
        opacity: 1;
        overflow: hidden;
    }
 
    .members{
        display: flex;
        background-color:rgba(201, 201, 201, 0.205);
        flex-direction: column;
        padding: 0 10px 25px 10px;
        column-gap: 25px;
        width: 100%;
        
    }
    .members h1{
        text-align: left;
    }
    .yellowSolidLine{
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        margin: 0;
        width: 200px;
    }
    .members p{
        text-align: left;
    }
    
    .doctors_cards{
        margin: 25px 0;
        display: flex;
        row-gap: 25px;
    }
    .doctor_card{
        position: relative;
        width: 280px;
        border-radius: 25px;
        background-color: white;
        text-align: center;
        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    }
    
}