.container{
    display: flex;
    background-color:white;
    flex-direction: column;
    align-items: center;
    padding: 0 90px;
}
.container h1{
    color: black;
    margin-top: 20px;
    position: relative;
    text-align: center;
}
.blueSolidLine{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 200px;
}
.container p{
    color: black;
}
.cards{
    margin-top: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 25px;
    row-gap: 25px;
    margin-bottom: 25px;
}
.card{
    display: flex;
    column-gap: 15px;
    transition: all 0.3s; 
    background-color:rgba(71, 174, 221, 0.205);
    background-color:rgb(250, 250, 250);
    border-radius: 15px;
    padding: 15px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    position: relative;
    overflow: hidden;
    cursor: default;
    
}
.card .card_img{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    transition: all 0.3s;
}
.card .card_img img{
    width: 70px;
    height: 70px; 
    transition: .6s all ease;   
}
.card:hover .card_img img{
    scale: 1.2;
    background-color: white; 
    border-radius: 50%;
    transition: .6s all ease;
}
.card h1{
    margin-top: 25px;
    font-size: 1rem;
    color: black;
}



@media(max-width:915px){
    .container{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        column-gap: 50px;
        row-gap: 50px;
        padding: 0 10px;
    }
    .container h1{
        color: black;
        margin-top: 25px;
        margin-bottom: -50px;
    }
    .container p{
        color: black;
        margin-top: 20px;
        margin-bottom: -15px;
        text-align: center;
    }

    .cards{
        margin-top: 0;
        display: flex;
        column-gap: 25px;
        row-gap: 25px;
        justify-content: flex-start;
    }
    .card{
        width: 100%;
        display: flex;
        transition: all 0.3s; 
        border-radius: 15px;
        padding: 15px;
    }
    .card:hover{
        scale: 1;     
    }
    .card:hover .card_img img{
        scale: 1;
        background-color: white; 
        border-radius: 50%;
        transition: .6s all ease;
    }
    
}
    




