.container{
    display: flex;
    /*background-color:rgba(71, 174, 221, 0.781);*/
    /*background-image: url(/public/images/image.png);*/
    background: rgb(255,255,255);
    background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgb(208, 221, 255) 76%);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    align-items: center;
    height: 80vh;
    padding: 0 90px;
    justify-content: space-around;
    width: 100%;
    overflow: hidden;
    position: relative;
}
.title{
    width: 50%;
}
span{
    color: rgb(33, 41, 160);
    position: relative;
    background-color: #ffb01ede;
}
h1{
    font-size: 3rem;
    font-weight: bold;
    text-transform: uppercase;
    color: black;
    position: relative;
}
.blueRetengleImg{
    position: absolute;
    bottom: 0;
    right: 100px;
}
p{
    color: black;
    margin: 15px 0;
    margin-bottom: 30px;
    opacity: 0.8;
}
.img{
    width: 50%;
    position: relative;
}
.mainImg1{
    width: 300px;
    height: 320px;
    position: absolute;
    bottom: -70px;
    right: 40px;
    animation:mainImgAnimation1 6s infinite ease-in-out 10s forwards ;
}
@keyframes mainImgAnimation1{
    0%{
        scale: 1;
    }
    50%{
        scale: 1.2;
    }
    100%{
        scale: 1;
    }
}
.mainImg2{
    width: 250px;
    height: 260px;
    position: absolute;
    top: -100px;
    left: 0;
    animation:mainImgAnimation2 6s infinite ease-in-out 5s forwards ;
}
@keyframes mainImgAnimation2{
    0%{
        scale: 1;
    }
    50%{
        scale: .8;
    }
    100%{
        scale: 1;
    }
}
.mainImg3{
    width: 200px;
    height: 210px;
    position: absolute;
    top: 50px;
    right: 130px;
    animation:mainImgAnimation3 5s infinite ease-in-out 5s forwards ;
}
@keyframes mainImgAnimation3{
    0%{
        scale: .8;
    }
    50%{
        scale: .6;
    }
    100%{
        scale: .8;
    }
}
.donate_button{
    color: black;
    background-color: #FFB21E;
    padding: 3px 20px 6px 20px;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
    border-radius: 5px;
    background-image: url(./../images/heart.png);
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: bottom 8px left -20px;
    filter: sepia(0);
    z-index: 50;
    position: relative;
    box-shadow: rgba(255, 255, 255, 0.1) 0px 1px 1px 0px inset, rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}
.donate_button:hover{
    color: white;
    background-color: rgb(33, 41, 160);
    padding: 3px 20px 6px 80px;
    background-image: url(./../images/heart.png);
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: bottom 8px left 20px;
    
}



@media(max-width:915px){
    .container{
        height: 90vh;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-direction: column;
        column-gap: 50px;
        row-gap: 50px;
        padding: 0 10px;
    }
    .title{
        width: 100%;
        margin-top: 0;
    }
    h1{
        font-size: 2.5rem;
        margin-top: 10px;
    }
    .img{
        width: 100%;
        position: relative;
        
    }
    .mainImg1{
        width: 100%;
        height: 100%;
        position: relative;
    }
    .mainImg2{
        display: none;
        width: 250px;
        height: 260px;
        position: absolute;
        top: 150px;
        left: -100px;
    }
    .mainImg3{
        display: none;
        width: 200px;
        height: 210px;
        position: absolute;
        top: 300px;
        right: -70px;
    }   
 
    
}