.news_card{
    display: flex;
    background-color:rgb(250, 250, 250);
    width: 96%;
    padding: 15px;
    border-radius: 15px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    cursor: default;
}
.title{
    color: black;
    font-size: 22px;
}
.description{
    color: black;
    overflow: hidden;
    transition: .5s all ease;
    height: 80px;
}
.description_active{
    color: black;
    overflow: hidden;
    transition: .5s all ease;
    height: auto;
}
.bottom_wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.date{
    color: black;
    padding: 0;
    margin: 0;
}
.more{
    width: 80px;
    cursor: pointer;
    padding: 5px;
    border-radius: 8px;
    border-width: 0px;
    background-color: rgb(255, 219, 13);
    color: black;
    transition: .3s all ease;
}
.more:hover{
    background-color: rgb(33, 41, 160);
    color: white;
    transition: .3s all ease;
}



