.container{
    position: relative;
    height: 80vh;
    padding: 0 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px 0;
    overflow: hidden;
    z-index: 90;

}
.container h2{
    color: black;
    z-index: 99;
}
.title{
    display: flex;
    justify-content: space-around;
    padding: 25px 0;
    column-gap: 25px;
    width: 100%;
}
.title .donate_page{
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 15px 15px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    margin-top: 50px;
    margin-bottom: 50px;
    border-radius: 15px;
    background-color:rgba(208, 221, 255);
    transition: .3s all;
    z-index: 98;
    position: relative;
}
.title .donate_page:hover{
    
    transition: .3s all;
    z-index: 98;
    background-color: rgb(255, 219, 13);
}
.title .donate_page .text{
    text-align: center;
}
.img1{
    width: 200px;
    height: 200px;
    position: absolute;
    bottom:5%;
    right: 5%;
    animation: img1animation 20s infinite ease ;
    z-index: 5;
}
@keyframes img1animation {
    0%{
        scale: 0;
        opacity: 0;
    }
    80%{
        scale: 1.8;
        opacity: 1;
    }
    100%{
        opacity: 0;
        scale: 1.8;
    }
}
.img2{
    width: 200px;
    height: 200px;
    position: absolute;
    top: 5%;
    left: 5%;
    animation: img2animation 10s infinite ease-out 10s forwards;
    z-index: 6;
}
@keyframes img2animation {
    0%{
        scale: 0;
        opacity: 0;
        animation-duration: 3s;
    }
    80%{
        scale: 1.8;
        opacity: 1;
    }
    100%{
        opacity: 0;
        scale: 1.8;
    }
}

.img3{
    width: 200px;
    height: 200px;
    position: absolute;
    top: 50%;
    left: 25%;
    animation: img3animation 15s infinite ease-out 10s forwards;
    z-index: 7;
}
@keyframes img3animation {
    0%{
        scale: 0;
        opacity: 0;
    }
    80%{
        scale: 2.5;
        opacity: 1;
    }
    100%{
        opacity: 0;
        scale: 2.5;
    }
}
.img4{
    width: 200px;
    height: 200px;
    position: absolute;
    top: 0%;
    right: 25%;
    animation: img4animation 20s infinite ease-out 10s forwards;
    z-index: 8;
}
@keyframes img4animation {
    0%{
        scale: 0;
        opacity: 0;
    }
    80%{
        scale: 2.7;
        opacity: 1;
    }
    100%{
        opacity: 0;
        scale: 2.7;
    }
}


@media(max-width:915px){
    .container{
        position: relative;
        padding: 0 10px;
        display: flex;
        padding: 0 10px;
        text-align: center;
    
    }
    .title .donate_page{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        padding: 15px 15px;
        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
        margin-top: 0px;
        margin-bottom: 25px;
        border-radius: 15px;
    }

}


